<template>
  <div class="goods">
    <div class="home-contend">
      <transition name="slide-left">
        <div v-if="shown" class="slideCon">
          <!--<header>
            <Icon type="home" size="25"></Icon>
            <span>房间</span>
          </header>-->
          <div class="addCenter noList" v-if="shopList.length===0">
            <Icon type="ios-home-outline" size="50"></Icon>
            <p>添加门店后，才可以创建房间哦</p>
            <a href="JavaScript:void(0)" @click="add" class="icon">
              <i class="iconfont icon-iconjia"></i>
              <span>添加门店</span>
            </a>
          </div>
          <div class="left-con" v-else>
            <div class="selected-shop" v-if="shopList.length>1">
              <div class="selected-wrapper">
                <p class="text">选择门店:</p>
                <div class="selected-item" v-for="(btnItem,index) in shopList">
                  <span class="btn" @click="selectMeal(btnItem,index)">{{btnItem.stores_name}}</span>
                  <div class="triangle" v-if="index === currentChoose">
                    <div class="triangle-inner" ></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="addCenter">
              <a href="JavaScript:void(0)" @click="add">
                <i class="iconfont icon-iconjia"></i>
                <span>添加房间</span>
              </a>
            </div>
            <div class="home-list">
              <div class="list-item" v-for="(item,index) in roomList" :key="index">
                <div class="list-con">
                  <div class="left">
                    <div class="img-box">
                      <img :src="item.photo" alt="">
                    </div>
                    <div class="img-msg">
                      <h3>{{item.room_type}}</h3>
                      <p class="light">{{item.bed_type}}</p>
                      <div class="hotel-room-amenities">
                        <img v-for="imgItem in item.facilities" :src="imgItem.imgUrl | imgFilters" alt="">
                      </div>
                    </div>
                  </div>
                  <div class="right">
                    <h3>￥{{item.price}}</h3>
                    <p class="light">{{item.number}}间</p>
                  </div>
                </div>
                <div class="item-mask">
                  <div class="mask-container">
                    <div class="editor-btn" @click="changeRoom(item)">编辑房间</div>
                  </div>
                  <div class="mask-close">
                    <div class="close-btn" @click="deleteRoom(item,index)">
                      <Icon type="close-round" size="13" color="#fff"></Icon>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </transition>
      <div class="slideCon">
        <transition name="slide-right">
          <solid v-if="show" :typeList="mealId" :roomId="roomId"></solid>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
   import solid from './hotelHomeSlide.vue'
  export default {
    data() {
      return {
        shutShow:true,
        shown: true,
        show: false,
        roomList:[],
        shopList:[],// 门店列表  当数量大于1 时候，出现选择门店区域
        currentChoose:0,// 当前选中门店,
//        房间id
        mealId:"",
//        0 添加 其他修改
        roomId:"",
      }
    },
    methods: {
      changeRoom(item){
//          房间id
        this.roomId = item.id;
        this.$store.commit('setStatus', false);
        this.shown = false;
        this.show = true;
      },
//        选择门店
      selectMeal(value,index){
        this.currentChoose = index ;
        this.mealId = value.id;
        this.getHotelList(value.id);
      },
//      删除room
      deleteRoom(item,index){
        this.$Modal.confirm({
          title: '确认删除',
          content: '确认要删除该房间吗?',
          onOk: () => {
              this.post("custom/Hotel/rooms_delete",{
                  id:item.id,
                  stores_id: this.mealId
              }).then(res => {
                  if(res.data.code===10000){
                    const content = res.data.msg;
                    this.$Modal.success({
                      title: "成功",
                      content: content
                    });
                    this.roomList.splice(index,1)
                  }else{
                    const content = res.data.msg;
                    this.$Modal.error({
                      title: "失败",
                      content: content
                    });
                  }
              })
          }
        });
      },
      add() {
        this.roomId = 0;
        this.$store.commit('setStatus', false);
        this.$store.dispatch('imgLists', []);
        this.shown = false;
        this.show = true;
      },
//      获取酒店的列表
      getHotelList(id){
          this.post("custom/Hotel/rooms_list",{
            stores_id :id
          }).then(res => {
              if(res.data.code === 10000){
                  this.roomList = res.data.data;
                  if(this.roomList.length !== 0){
                    this.roomList.forEach(item =>{
                      item["photo"] = "http://weapp.xiguawenhua.com/" +  item["photo"].split(",")[0];
                      item['facilities'] =JSON.parse(item['facilities']);
                    });
                  }
              }else{
                  const content = res.data.msg;
                  this.$Modal.error({
                    title: "失败",
                    content: content
                  });
            }
          })
      },
      getMealList(){
        this.post("custom/Hotel/list_stores ",{}).then(res => {
          if (res.data.code === 10000){
            this.shopList = res.data.data;
            if(this.shopList.length !== 0){
//              this.shopList.forEach( item =>{
//                item["flag"] = false;
//              });
              this.getHotelList(this.shopList[this.currentChoose].id);
              this.mealId = this.shopList[this.currentChoose].id;
            }
          }else{
            const content = res.data.msg;
            this.$Modal.error({
              title: "失败",
              content: content
            });
          }
        })
      }
    },
    mounted() {
//      this.getMealList();
    },
    created() {
//        获取酒店及分类列表
      this.post("custom/Hotel/get_stores_rooms_detail",{}).then(res => {
         if(res.data.code === 10000){
              let roomSort = res.data.data;
              if(roomSort.length !== 0){
                window.frames[0].postMessage(roomSort,"*");
              }
         }
      })
    },
    activated(){
      this.getMealList();
    },
    components: {
      solid
    },
    filters:{
      imgFilters(value){
        let name = value?value:'noImg.png';
        return `/static/image/${name}`
      }
    }
  }
</script>
<style scoped>
  .slide-left-enter-active,.slide-left-leave-active{
    transition: all 0.2s ease;
    opacity: 1;
    transform: translateX(0);
  }
  .slide-left-enter,.slide-left-leave{
    opacity: 0;
    transform: translateX(-200%);
  }
  .slide-right-enter-active,.slide-right-leave-active{
    transition: all 1s ease-in;
    transform: translateX(0);
  }
  .slide-right-enter,.slide-right-leave{
    transform: translateX(200%);
  }
  .home-contend{
    position: relative;
  }
  .home-contend .slideCon{
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
  header {
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }
  header i{
    margin-right: 10px
  }
  header>span {
    font-size: 18px;
    display: inline-block;
    line-height: 18px;
  }
  .addCenter{
    padding: 20px 0;
    border-bottom: 1px solid #eee;
  }
  .addCenter>a{
    display: inline-block;
    padding: 0.1rem 0.2rem;
    background: #59A2ED;
    color: white;
    font-size: 0.14rem;
    border-radius: 0.05rem
  }
  .noList{
    margin-top: 80px;
    text-align: center;
    color: #606060;
  }
  .noList>p:nth-child(2){
    margin: 0.1rem 0 0.05rem 0;
    font-size: 18px;
    font-weight: 800;
  }
  /*门店选择*/
  .selected-shop{overflow-x: scroll;overflow-y: hidden;}
  .selected-shop::-webkit-scrollbar{
    width:5px;
    height:6px;
  }
  .selected-shop::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .selected-wrapper{align-items: center;border-bottom: 1px solid #ddd;display: flex;padding: 15px 0;white-space: nowrap;}
  .selected-wrapper .text{color: #888b94;font-size: 12px;margin: 0 10px;}
  .selected-wrapper .selected-item{margin-right: 10px;position: relative;}
  .selected-wrapper .selected-item .btn{background: #59A2ED; color: #FFFBFF;font-size: 12px;  padding: 8px 12px;
    margin: 3px; transition: all 0.15s;text-decoration: none;font-weight: bold;  -webkit-appearance: none;border-radius: 4px;cursor: pointer;
    text-transform: uppercase;min-width: 80px;display: inline-block;text-align: center;border: 1px solid #ccc;line-height: 1;}
  .selected-wrapper .selected-item .btn:hover{background-color: #eee;border-color: #ccc;}
  .triangle, .triangle-inner{
    border: 27px solid #fff;
    height: 0;
    width: 0;
  }
  .selected-wrapper .selected-item .triangle{
    border-color: transparent transparent #ddd;
    border-width: 8px;
    left: 50%;
    margin-left: -8px;
    position: absolute;
    top: 36px;
  }
  .triangle {
     border-color: transparent transparent transparent #ebecee;
    bottom: 0;
    margin-left: -8px;
    position: absolute;
    right: 45%;
  }
  .selected-wrapper .selected-item .triangle-inner{
    border-color: transparent transparent #fff;
    border-width: 8px;
    left: 50%;
    margin-left: -8px;
    position: absolute;
    top: -6px;
  }
  .icon{
    background: #59A2ED;
  }
  /*门店选择结束*/
  .list-item{position: relative;border-bottom: 1px solid #ddd;transition: all .8s ease}
  .list-item:hover .item-mask{display: block}
  .list-item .list-con{ display: flex;justify-content: space-between;padding: 15px;background: white; }
  .list-item .item-mask{width:100%;position:absolute;top:0;height:100%;left:0;background-color:rgba(0,0,0,.1);z-index:100;display: none;}
  .item-mask .mask-container{display:flex;width:100%;height:100%;justify-content:center;align-items:center;}
  .mask-container .editor-btn{ background-color: #272727;border-radius: 3px;color: #e2e4e7;padding:3px 14px;cursor:pointer;font-size: 14px;}
  .item-mask .mask-close{ left: auto;position: absolute;right: 4px;top: 4px;z-index: 102;}
  .mask-close .close-btn{background-color: rgba(0,0,0,.6); background-position: 50% 50%;background-repeat: no-repeat;border-radius: 3px;cursor: pointer;padding: 2px 5px;}
  .list-item h3{color: #52616a;font-size: 16px;margin-bottom: 10px;margin-right: 10px;text-transform: uppercase;font-weight: 600;
    line-height:1;padding:0}
  .list-item .left{display: flex;}
  .list-item .left .img-box{align-items: center;display: flex; height: 90px;justify-content: center;margin-right: 16px;width: 120px;}
  .list-item .left .img-box img{border-radius: 5px;max-height: 90px;max-width: 120px;}
  .list-item .left .img-msg{padding-top: 8px;}
  .list-item .light{color: #9298a0;line-height: 1;margin-top: 0.2rem;}
  .list-item .left .hotel-room-amenities{margin-top: 10px;}
  .list-item .left .hotel-room-amenities img{margin-right: 8px;vertical-align: middle;width: 16px;}
  .list-item .right{padding-top: 8px;}
</style>



