                上传图片
                <p style="color: #dddddd">最多可增加十张图片</p>
              </div>-->
            </div>
            <div class="single-image-wrapper" v-else-if="images.length===1" @mouseover="shade">
              <div class="shade">
                <div class="shade-con">
                  <a href="javascript:void (0)" @click="upload">
                    <Icon type="loop" size="35" color="#f3f3f3" style="margin:0 0.25rem"></Icon>
                  </a>
                  <a href="javascript:void (0)" @click="deletePhoto(0)">
                    <Icon type="android-delete" size="35" color="#f3f3f3"></Icon>
                  </a>
                </div>
              </div>
              <div :style="{backgroundImage:'url('+images[0].pic+')'}" class="backgroundImage"></div>
              <!--<img :src="images[0].pic" alt="" style="width: 100%;height: 100%">-->
            </div>
            <div class="images-wrapper" v-else>
              <div class="add_image">
                <div class="image-item place-holder" @click="upload">+</div>
                <div class="image-item" v-for="(item,key) in images">
                  <div class="image-cover">
                    <div class="image-cover-con">
                      <div class="cover-item" v-if="key!==0">
                        <Tooltip content="将图片设置成封面图片" placement="top" transfer>
                          <a href="javascript:void(0)" @click="setImage(key)">
                            <Icon type="image" size="25" color="#f3f3f3"></Icon>
                          </a>
                        </Tooltip>
                      </div>
                      <a href="javascript:void (0)" @click="upload" class="cover-item">
                        <Icon type="loop" size="25" color="#f3f3f3" style="margin:0 0.25rem"></Icon>
                      </a>
                      <a href="javascript:void (0)" @click="deletePhoto(key)"class="cover-item">
                        <Icon type="android-delete" size="25" color="#f3f3f3"></Icon>
                      </a>
                    </div>
                  </div>
                  <img :src="item.pic" alt="">
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="right">
        <div class="right_rightItem">
    	   <div class="rightItem">
            <div class="item_hotel">
              <div class="item-title">
                <h3 style="width: 0.8rem;">房间类型</h3>
                <span  class="tipMsg">{{msgError.roomMsg}}</span>
              </div>
              <input type="text" style="margin-left: 0.2rem;" v-model.trim="roomType" placeholder="例如:单人间、标准间" @blur="change(roomType,'roomMsg')" ref="roomMsg"  @input="input('roomMsg')" />
            </div>
            <div class="item_hotel">
              <div class="item-title">
                <h3 style="width: 0.5rem;">数量</h3>
                <span class="tipMsg">{{msgError.numberMsg}}</span>
              </div>
              <input v-model.trim="roomNumber" ref="numberMsg" type="text" @input="input('numberMsg')" @blur="change(roomNumber,'numberMsg')" placeholder="房间数" />
            </div>
            <div class="item_hotel">
              <div class="item-title">
                <h3 style="width: 0.5rem;">价格</h3>
                <span class="tipMsg">{{msgError.priceMsg}}</span>
              </div>
              <div class="input-box">
                <span class="cny_span">CNY</span>
                <span class="cny_zi">/次</span>
                <input  v-model.trim="price" style="padding-left: 0.5rem;margin-left: 0.2rem;" type="text" @input="input('priceMsg')" @blur="change(price,'priceMsg')" ref="priceMsg"/>
                <!--<span class="priceTip">你可以在"价格调整"里灵活设置假日价格</span>-->
              </div>
              
            </div>
        </div>
            <p style="border: 1px solid #E8E8E8;height: 1px;width: 98%;margin-top: 0.2rem;"></p>
          <div class="rightItem">
            <div class="item_hotel">
              <div class="item-title">
                <h3>容纳人数</h3>
                <span  class="tipMsg">{{msgError.personMsg}}</span>
              </div>
              <div class="input-box">
                <span class="cny_zi">人</span>
                <input type="text" style="margin-left: 0.2rem;" v-model.trim="roomPerson" placeholder="该房间可容纳最大人数" @input="input('personMsg')" @blur="change(roomPerson,'personMsg')" ref="personMsg"/>
              </div>
            </div>
            <div class="item_hotel">
              <div class="item-title">
                <h3>房间面积</h3>
                <span class="tipMsg">{{msgError.areaMsg}}</span>
              </div>
              <div class="input-box">
                <span class="cny_zi">平米</span>
                <input v-model.trim="roomArea" style="margin-left: 0.2rem;" type="text" placeholder="大约数值" @input="input('areaMsg')" @blur="change(roomArea,'areaMsg')" ref="areaMsg"/>
              </div>
            </div>
            <div class="item_hotel">
              <div class="item-title">
                <h3>床铺类型</h3>
                <span class="tipMsg">{{msgError.bedMsg}}</span>
              </div>
              <input type="text" style="margin-left: 0.2rem;" v-model.trim="bedType" placeholder="例如: 1.5米双人床" @input="input('bedMsg')" @blur="change(bedType,'bedMsg')" ref="bedMsg"/>
            </div>
          
          </div>
          <div class="rightItem">
            
          </div>
          <div class="rightItem">
            <div class="item_hotel">
              <div class="item-title">
                <h3>简短描述</h3>
                <span class="tipMsg">{{textNum}}字以内</span>
              </div>
              <textarea style="margin-left: 0.2rem;" v-model.trim="text" cols="30" rows="10" class="text" placeholder="简单描述下你的服务吧。 (选填)" ref="text" @focus="textF"  @input="inputLength" @blur="textareaBlur"></textarea>
            </div>
          </div>
        </div>
        
        <div class="others amenities right_rightItem">
            <h3>便利设施</h3>
            <div class="smallTip">点击选择为客户提供的基础便利设施。</div>
            <div class="amenities-list">
              <div class="amenities-tab" v-for="(item,index) in amenities" :key="index" :class="{selected:item.selected}" @click="item.selected=!item.selected">
                <img :src="item.imgUrl | imgFilters" alt="">
                <span class="amenities-text">{{item.name}}</span>
                <div class="selected-zone" v-if="item.selected">
                  <div class="amenities-bg"></div>
                  <div class="amenities-check">
                    <Icon type="checkmark-round" size="14" color="#fff"></Icon>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="others desc right_rightItem">
            <h3>详情描述</h3>
            <div class="info">
              <Checkbox v-model="content_show" false-value="0" true-value="1"><span style="font-size: 0.14rem">显示详情描述信息，如：服务条款说明、附近景点、交通、介绍等。</span></Checkbox>
            </div>
            <textEditor @inputInfo="roomInfo" v-if="content_show==='1'" :value="editorContent"></textEditor>
          </div>
          <div class="rightItem right_rightItem" v-show = "outletShow">
            <div class="right-con apply-block">
               <h3 style="width: 1.9rem;">将该房间添加到以下门店</h3>
              <div class="radio-box">
                <span class="label" v-show="selectValue===''&&radioValue==='1'">请至少选择一家门店</span>
                <div class="radio-content">
                  <RadioGroup v-model="radioValue" vertical @on-change="radioChange">
                    <Radio  label="1">特定门店</Radio>
                    <div class="storeList" v-if="radioValue==='1'">
                        <Select v-model="selectValue" style="width: 200px" multiple>
                          <Option v-for="item in selectList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                        </Select>
                    </div>
                    <Radio label="2">当前所有门店</Radio>
                  </RadioGroup>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
    <footer>
      <a href="javaScript:void(0)" @click="cancel">
        < 取消</a>
      <a href="javaScript:void(0)" @click="submit" v-if="!load">保存</a>
      <a href="javaScript:void(0)" v-if="load">保存 <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%" ></a>
    </footer>
    <modal ref="upload"></modal>
  </div>
</template>
<script>
  import modal from '../upload/modal.vue'
  import textEditor from '../textEditor.vue'
  export default {
    data() {
      return {
//          编辑器内容
        editorContent:'',
        load:false,
        id:'',
        shadeShow: false,
        outletShow:true,
        //提示信息
        single: true,
        singleInfo: false,
        text: '',
        textNum: 600,
        imgList: [],
//        images:[],
        desc: '',
        sorts: '',
        content_show: "0",
        content: "",
        src: [],
        price: '',// 价格
        roomType: "", // 房间类型
        roomNumber:"", // 房间数量
        roomPerson:"", // 房间容纳人数
        roomArea:"" ,// 房间面积
        bedType:"",// 床铺类型
//        错误信息提示
        msgError:{
          priceMsg:"",
          roomMsg:"",
          numberMsg:"",
          personMsg:"",
          areaMsg:"",
          bedMsg:"",
        },
        // 房间设施
        amenities:[
          {imgUrl:'icon-hotel-tv.png',name:'电视',selected:true},
          {imgUrl:'icon-hotel-tel.png',name:'电话',selected:true},
          {imgUrl:'icon-hotel-kitchen.png',name:'厨房',selected:false},
          {imgUrl:'icon-hotel-air.png',name:'空调',selected:true},
          {imgUrl:'icon-hotel-shower.png',name:'沐浴',selected:true},
          {imgUrl:'icon-hotel-pet.png',name:'允许宠物',selected:false},
          {imgUrl:'icon-hotel-wifi.png',name:'网络',selected:true},
          {imgUrl:'icon-hotel-sofa.png',name:'沙发',selected:false},
          {imgUrl:'icon-hotel-parking.png',name:'停车场',selected:false},
          {imgUrl:'icon-hotel-smoking.png',name:'吸烟区',selected:false}],
        radioValue:"1" , // 下方  radio选种植
        selectValue:[], // SELECT  选中的值
        selectList:[] // select  列表
      }
    },
    computed: {
     images(){
        return this.$store.getters.getImgLists;
      }
    },
    props:["typeList","roomId"],
    methods: {
//        获取房间详情
      roomInfo(val){
          this.content = val;
      },
//      radio  change改变
      radioChange(value){
        if(value === '2'){
          this.selectValue = [];
        }
      },
      goodsInfo(val) {
        this.content = val;
      },

      setImage(key) {
        let src = this.images[0];
        this.images[0] = this.images[key];
        this.images.splice(key, 1, src)
      },
      deletePhoto(key) {
       if(key!==""){
         this.images.splice(key,1)
       }
      },
      shade() {
        this.shadeShow = true;
      },
      //       添加图片
      addPhoto() {
        this.$refs.upload.modal = true;
      },
      change1() {},
      //上传图片
      upload() {
        this.$refs.upload.modal = true;
        this.$store.dispatch('goodsPhoto', 1);
      },
      addSort() {
        this.$router.push('serveSorts')
      },
      inputLength() {
        //        this.textNum-=this.text.length;
      },
      //          聚焦
      textF() {
        this.$refs.text.style.borderColor = 'skyblue';
      },
      textareaBlur(){
        this.$refs.text.style.borderColor = 'gainsboro';
      },
      //      输入设置
      input(value) {
        this.$refs[value].style.borderColor = 'skyblue';
        this.msgError[value] = ''
      },
      //      检测
      change(value,error) {
        if(value) {
          this.$refs[error].style.borderColor = 'gainsboro';
          this.msgError[error] = ''
        } else {
          this.$refs[error].style.borderColor = '#e64751';
          let msgText = "";
         switch (error){
           case "roomMsg":
             msgText = '请填写房间类型';
             break;
           case 'numberMsg':
             msgText = '请填写房间数量';
             break;
           case 'priceMsg':
             msgText = '请填写价格';
             break;
           case 'personMsg':
             msgText = '请填写容纳人数';
             break;
           case 'areaMsg':
             msgText = '请填写房间面积';
             break;
           case 'bedMsg':
             msgText = '请填写床铺类型';
             break;
           default:
             break;
         }
          this.msgError[error] = msgText
        }
      },
//   删除服务
      cancel() {
        this.$store.commit('setStatus', true);
        this.$parent.shown = true;
        this.$parent.show = false;
        this.$parent.changeShow = false;
      },
//    添加房间/修改房间
      saveRoom(url){
        let arr = [];
        arr = this.images.map(item => {
          return item = item.pic.slice(29)
        });
//          房间设施
        let amenities = this.amenities.filter( item => item.selected === true);
        this.post(url, {
          id:this.roomId,
          photo: arr.toString(),
          room_type: this.roomType,
          number: this.roomNumber,
          price: this.price,
          man: this.roomPerson,
          area: this.roomArea,
          bed_type : this.bedType,
          describe: this.text,
          facilities: JSON.stringify(amenities),
          detail: JSON.stringify(this.content),
          stores_type : this.radioValue,
          stores_id  : this.selectValue.toString(),
        }).then(res => {
          this.load = false;
          if(res.data.code === 10000) {
            const content = res.data.msg;
            this.$Modal.success({
              title: "保存成功",
              content: content
            });
            this.cancel();
            this.$parent.getHotelList(this.typeList);
          } else {
            const content = res.data.msg;
            this.$Modal.error({
              title: "保存失败",
              content: content
            });
          }
        })
      },
//      获取门店列表
      getMealList(){
          this.post("custom/Hotel/list_stores",{}).then( res => {
              if(res.data.code === 10000){
                this.selectList = res.data.data;
                this.selectList.forEach( item => {
                    item["value"] = item.id;
                    item["label"] = item.stores_name;
                })
              }
          })
      },
      submit() {
          this.load = true;
          if(this.roomId === 0){
//            添加
            this.saveRoom("custom/Hotel/rooms_add")
          }else{
//修改
            this.saveRoom("custom/Hotel/rooms_update")
          }
      },
      // 设施 tab click
//      tabClick(index){
//        this.amenities[index].selected = ! this.amenities[index].selected
//      }
    },
    components: {
      modal,
      textEditor
    },
    watch: {
      typeList(val){
          return val
      },
      text(newVal, oldVal) {
        this.textNum -= (newVal.length - oldVal.length)
      },
      sortsInfo(val) {
        return val;
      }
    },
    mounted() {
//        默认选中某个
        this.selectValue.push(this.typeList);
        this.getMealList();
        if(this.roomId === 0){
//            添加
          return false;
        }else{
//            修改
          this.outletShow = false;
          this.post("custom/Hotel/get_rooms",{
              id:this.roomId
          }).then(res => {
             if(res.data.code === 10000){
               this.roomType = res.data.data.room_type;
               this.roomNumber = res.data.data.number;
               this.price = res.data.data.price;
               this.roomPerson = res.data.data.man;
               this.roomArea = res.data.data.area;
               this.bedType = res.data.data. bed_type;
               this.text = res.data.data.describe;
//               设施恢复
               let arr = JSON.parse(res.data.data.facilities);
               arr.forEach( item => {
                  this.amenities.forEach( (list,key) => {
                      if(item.name === list.name){
                          this.amenities[key].selected = true;
                      }
                  })
               });
//               商品详情
               if(res.data.data.detail){
                 this.content_show = "1";
                 this.editorContent = JSON.parse(res.data.data.detail);
               }
               this.radioValue = res.data.data.stores_type;
               let imgList = [];
               res.data.data.photo.split(',').forEach(item=>{
                 let obj={};
                 obj["pic"]="http://weapp.xiguawenhua.com/"+item;
                 obj["show"]=false;
                 imgList.push(obj);
               });
               this.$store.dispatch('imgLists',imgList);
             }
          })
        }
    },
    filters:{
      imgFilters(value){
        return value? `./static/image/${value}`:'./static/image/noImg.png'
      }
    }
  }
</script>
