    width: 0.8rem;
    border-left:3px solid #25B3EF;
    border-top-left-radius: 0.03rem;
    border-bottom-left-radius: 0.03rem;
  }
  .others{
    margin-top: 20px;
    border-bottom: 1px solid #eee;
  }
.amenities{
  padding: 24px 0;
  border-top: 1px solid #eee;
}
.amenities .smallTip{
  margin-bottom: 14px;
  margin-left: 0.2rem;
}
.amenities-list{
  margin-right: 20px;
  margin-top: 8px;
  position: relative;
  flex: 1 1 0%;
  line-height: 20px;
  text-align: left;
  margin-left: 0.2rem;
}
  .amenities-list .selected{
    border-color: #15bbfc;
  }
.amenities-tab{
  border: 1px solid #e2e4e7;
  border-radius: 3px;
  cursor: pointer;
  display: inline-block;
  margin-bottom: 14px;
  margin-right: 12px;
  padding: 8px 13px;
  position: relative;
}
  .amenities-tab img{
    margin-right: 8px;
    vertical-align: middle;
    width: 16px;
  }
  .amenities-tab .amenities-text{
    line-height: 20px;
  }
  .amenities-tab .amenities-bg{
    border-bottom: 20px solid #15bbfc;
    border-left: 25px solid transparent;
    bottom: 0;
    height: 0;
    position: absolute;
    right: 0;
    width: 0;
  }
  .amenities-check{
    bottom: -4px;
    position: absolute;
    right: 2px;
  }
  .right input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 10px;
  }

  .right .text {
    width: 500px;
    padding: 0.1rem 0.1rem;
    height: 150px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    display: inline-block;
  }
  /*.left>div{height: 30vh;}*/

  .price p {
    padding: 0.1rem 0
  }
  .left{
     margin-top: 0.2rem;
      padding-left: 0.2rem;
      background: white;
      padding-bottom: 0.2rem;
      box-shadow: 5px 5px 10px #E7E7E7;
  }
  .left input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }
  footer {
    position: fixed;
    bottom: 0;
    padding: 0.2rem 0.3rem;
    margin-left: -0.2rem;
    background: #fff;
    box-shadow: 0 -2px 3px #ddd;
    box-sizing: border-box;
    width: calc(100vw - 2.6rem);
    text-align: center;
  }

  footer>a {
    color: black;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    background: #eee;
    border-radius: 0.05rem;
  }

  footer>a:nth-child(1) {
    float: left;
  }

  footer>a:nth-child(2) {
    color: white;
    background: #93b719;
  }

  .cny_span {
    position: absolute;
    font-size: 12px;
    top: 50%;
    left: 30px;
    display: inline-block;
    width: 30px;
    height: 15px;
    line-height: 15px;
    text-align: center;
    color: #ACB2B4;
    border: 1px solid #A9AEB2;
    border-radius: 4px;
    transform: translateY(-50%);
  }

  .cny_zi {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
  }
  .img_ho{
    text-align: center;
    display: inline-block;
    font-size: 0.4rem;
    margin-top: 0.2rem;
    color: #DCDCDC;
  }
</style>



